.review {
     margin-top: 150px;   
    &_h2 {
        .h2();
        span {
            color: #ce874e;
        }
    }
    &_slider {
        margin-top: 80px;
    }
    &_p {
        color: @gray;
        &.figure {
            position: relative;
            &::after {
                content: '';
                position: absolute;
                top: 0px; left: 0;
                background: url(../img/figure.png) no-repeat;
                width: 40px;
                height: 59px;
                z-index: -1;
            }
            &::before {
                content: '';
                position: absolute;
                top: 0; left: 50px;
                background: url(../img/figure.png) no-repeat;
                width: 40px;
                height: 59px;
                z-index: -1;
            }
        }
    }
    &_bottom {
        margin-top: 35px;
        display: flex;
        align-items: center;
        figure {
            margin: 0;
        }
        div {
            margin-left: 20px;
        }
    }
    .slick-prev {
        top: auto;
        bottom: 0px;
        right: 35px;
        left: auto;
    }
    .slick-next {
        top: auto;
        bottom: 0px;
        right: 5px;
        left: auto;
    }
}