.master {
    margin-top: 150px;
    &_h2 {
        .h2();
        span {
            color: #ce874e;
        }
    }
    &_wrap {
        margin-top: 100px;
        .row-flex();
    }
    &_item {
        .col();
        .size(3);
        text-align: center;
        &:nth-child(1),
        &:nth-child(3) {
            position: relative;
            top: -50px;
        }
    }
    &_h3 {
        .medium();
        margin-top: 35px;
        padding-bottom: 20px;
        font-size: 18px;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            bottom: 0; left: 50%;
            transform: translate(-50%);
            width: 35px; height: 2px;
            background-color: @crap;
        }
    }
    &_p {
        margin-top: 25px;
        color: @gray;
        .medium();
    }
    &_social {
        margin-top: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        &--item {
            margin: 0 10px;
            img {
                transform: rotate(90deg);
            }
        }
    }
}