.about {
    &_wrap {
        .row-flex();
        justify-content: space-between;
    }
    &_left {
        .col();
        .size(6);
        margin-top: 200px;
        display: flex;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            top: -25px; left: -90px;
            background-color: #1d1b1e;
            width: 525px; height: 2px;
            z-index: -1;
        }
        &::before {
            content: '';
            position: absolute;
            top: -25px; left: 300px;
            background-color: @crap;
            width: 35px; height: 2px;
        }
    }
    &_img1 {
        position: relative;
        top: -50px;
    }
    &_img2 {
        margin-left: 30px;
    }
    &_right {
        .col();
        .size(5);
        border-left: 2px solid #1d1b1e;
        padding-top: 170px;
        padding-left: 25px;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            top: 180px; left: -2px;
            background-color: @crap;
            width: 2px; height: 35px;
        }
    }
    &_h2 {
        font-size: 50px;
        text-transform: uppercase;
        .medium();
    }
    &_p1 {
        font-size: 17px;
        margin: 50px 0 25px;
    }
    &_p2 {
        color: @gray;
        font-size: 17px;
    }
    &_pavloni {
        text-align: right;
    }
}