.contact {
    background: url(../img/contact.png) no-repeat;
    min-height: 810px;
    background-size: contain;
    background-position: bottom;
    &_wrap {
        .row-flex();
        justify-content: space-between;
    }
    &_left {
        .col();
        .size(6);
        padding-top: 150px;
        position: relative;
        &--img {
            position: relative;
        }
    }
    &_right {
        .col();
        .size(5);
        
        border-left: 2px solid #1d1b1e;
        padding-top: 150px;
        padding-left: 25px;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            top: 160px; left: -2px;
            background-color: @crap;
            width: 2px; height: 35px;
        }
    }
    &_text {
        position: absolute;
        top: 5px; left: 25px;
        &::before {
            content: '';
            position: absolute;
            top: -5px; left: -25px;
            background-color: @crap;
            width: 2px; height: 35px;
        }
    }
    &_a {
        display: block;
        margin-top: 10px;
        .medium();
        font-size: 25px;
    }
    &_h2 {
        font-size: 50px;
        color: #fff;
        .medium();
    }
    &_input {
        width: 100%;
        margin: 15px 0;
        background-color: transparent;
        display: block;
        border-bottom: 1px solid #fff;
        color: @gray;
        padding: 12px 5px;
        &::placeholder {
            color: @gray;
            font-size: 16px;
        }
        &.textarea {
            border: none;
            border-bottom: 1px solid #fff;
        }
    }
    &_btn {
        margin-top: 50px;
        .btn();
        cursor: pointer;
        color: #fff;
    }
    &_span {
        .medium();
        color: @gray;
        font-size: 18px;
        position: absolute;
        bottom: 90px; right: -90px;
        transform: rotate(90deg);
    }
}

.footer {
    &_container {
        display: flex;
        .container-full();
        justify-content: space-around;
        align-items: center;
        min-height: 100px;
    }
    &_p {
        text-transform: uppercase;
        color: @gray;
        font-size: 14px;
    }
    &_social {
        display: flex;
        align-items: center;
        justify-content: center;
        &--item {
            margin: 0 10px;
            display: flex;
            align-items: center;
            img {
                transform: rotate(90deg);
            }
            p {
                margin-left: 10px;
            }
        }
    }
}