.btn_mnu {
  width: 50px;
  height: 18px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  cursor: pointer;
  position: relative;
  z-index: 101;
}

.btn_row {
  height: 2px;
  width: 60%;
  margin: 2px 0;
  background-color: #fff;
  position: relative;
  transition: all .5s;
}

.btn_mnu .btn_row:nth-child(2) {
  width: 30%;
}

.btn_mnu.active .btn_row:first-child {
  transform: rotate(-135deg);
  top: 6px;
}

.btn_mnu.active .btn_row:last-child {
  transform: rotate(-225deg);
  top: -6px;
}

.btn_mnu.active .btn_row:nth-child(2) {
  opacity: 0;
}

#menu-burger {
  background-color: #181818;
  position: absolute;
  top: 0;
  left: -1000px;
  width: 50%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s all;
  z-index: 100;
}

#menu-burger.active {
  left: 0;
}

.menu-burger--li {
  margin: 20px 0;
}

.menu-burger--a {
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  display: inline-block;
}

.menu-burger--a::after {
  display: block;
  content: '';
  border-bottom: 1px solid #fff;
  transform: scaleX(0);
  transform-origin: 100% 50%;
  transition: transform 450ms ease-in-out;
}

.menu-burger--a:hover::after {
  transform: scaleX(1);
  transform-origin: 0% 50%;
}

.menu-burger--tel {
  position: absolute;
  top: 50px;
}

