.master_p {
  margin-top: 25px;
  color: #655e68;
  font-family: 'Proxima Nova Lt';
}

.master_social {
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.master_social--item {
  margin: 0 10px;
}

.master_social--item img {
  transform: rotate(90deg);
}

.contact {
  background: url(../img/contact.png) no-repeat;
  min-height: 810px;
  background-size: contain;
  background-position: bottom;
}

.contact_wrap {
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
  justify-content: space-between;
}

.contact_left {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(50% - 15px);
  padding-top: 150px;
  position: relative;
}

.contact_left--img {
  position: relative;
}

.contact_right {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
  width: calc(41.66666666666667% - 15px);
  border-left: 2px solid #1d1b1e;
  padding-top: 150px;
  padding-left: 25px;
  position: relative;
}

.contact_right::before {
  content: '';
  position: absolute;
  top: 160px;
  left: -2px;
  background-color: #7e5337;
  width: 2px;
  height: 35px;
}

.contact_text {
  position: absolute;
  top: 5px;
  left: 25px;
}

.contact_text::before {
  content: '';
  position: absolute;
  top: -5px;
  left: -25px;
  background-color: #7e5337;
  width: 2px;
  height: 35px;
}

.contact_a {
  display: block;
  margin-top: 10px;
  font-family: 'Proxima Nova Lt';
  font-size: 25px;
}

.contact_h2 {
  font-size: 50px;
  color: #fff;
  font-family: 'Proxima Nova Lt';
}

.contact_input {
  width: 100%;
  margin: 15px 0;
  background-color: transparent;
  display: block;
  border-bottom: 1px solid #fff;
  color: #655e68;
  padding: 12px 5px;
}

.contact_input::placeholder {
  color: #655e68;
  font-size: 16px;
}

.contact_input.textarea {
  border: none;
  border-bottom: 1px solid #fff;
}

.contact_btn {
  margin-top: 50px;
  display: inline-block;
  padding: 12px 20px;
  border: 1px solid #7e5337;
  text-transform: uppercase;
  font-size: 17px;
  transition: .3s all;
  cursor: pointer;
  color: #fff;
}

.contact_btn:hover {
  background-color: #7e5337;
}

.contact_span {
  font-family: 'Proxima Nova Lt';
  color: #655e68;
  font-size: 18px;
  position: absolute;
  bottom: 90px;
  right: -90px;
  transform: rotate(90deg);
}

.footer_container {
  display: flex;
  padding-left: 15px;
  padding-right: 15px;
  justify-content: space-around;
  align-items: center;
  min-height: 100px;
}

.footer_p {
  text-transform: uppercase;
  color: #655e68;
  font-size: 14px;
}

.footer_social {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer_social--item {
  margin: 0 10px;
  display: flex;
  align-items: center;
}

.footer_social--item img {
  transform: rotate(90deg);
}

.footer_social--item p {
  margin-left: 10px;
}

@media screen and (max-width: 1440px) {
  .header_left--span {
    display: none;
  }

  .header_right--li {
    margin: 0 10px;
  }

  .header_right--a {
    padding: 15px;
  }
}

@media screen and (max-width: 480px) {
  .menu-burger--li {
    margin: 10px 0;
  }

  .menu-burger--a {
    font-size: 15px;
  }
}