.intro {
    &_slider {
        &--item {
            height: 90vh;
            position: relative;
        }
        &--item1 {
            background: url(../img/intro.jpg) center/cover no-repeat;
        }
    }
    &_content {
        .shift-left(1);
        position: absolute;
        bottom: 65px;
    }
    &_title {
        font-size: 48px;
        .medium();
        span {
            color: #d08548;
        }
    }
    &_desc {
        margin-top: 10px;
        font-size: 25px;
        max-width: 550px;
        padding-left: 75px;
    }
    &_right {
        position: absolute;
        top: 0; left: 0;
        height: 90vh;
        width: 100px;
        background-color: #0e0c0f;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &_social {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        height: 300px;
        &--item {
            display: inline-block;
            transform: rotate(-90deg);
            margin: 0 50px;
            position: relative;
            z-index: 10;
            &::after {
                content: '';
                position: absolute;
            }
            &:nth-child(1)::before,
            &:nth-child(2)::before {
                content: '';
                position: absolute;
                width: 5px;
                height: 5px;
                background-color: #d08548;
                border-radius: 100%;
                top: 7px; left: -50px;
            }
            &:nth-child(3)::after {
                background-image: url(../img/fb.png);
                transform: rotate(90deg);
                width: 11px;
                height: 6px;
                top: 7px; left: -15px;
            }
            &:nth-child(2)::after {
                background-image: url(../img/inst.png);
                transform: rotate(90deg);
                width: 12px;
                height: 12px;
                top: 4px; left: -20px;
            }
            &:nth-child(1)::after {
                background-image: url(../img/vk.png);
                transform: rotate(90deg);
                width: 7px;
                height: 16px;
                top: 2px; left: -20px;
            }
        }
    }
    .slick-prev {
        top: auto;
        bottom: 50px;
        right: 75px;
        left: auto;
    }
    .slick-next {
        top: auto;
        bottom: 50px;
        right: 55px;
        left: auto;
    }
    .slick-dots {
        width: auto;
        top: 50%;
        transform: translate(0,-50%);
        left: auto;
        right: 25px;
        display: flex;
        flex-direction: column;
    }
    .slick-dots li {
        position: relative;
        width: auto;
        height: auto;
        margin: 10px 0;
        padding: 0;
        cursor: pointer;
        opacity: 1;
    }
    .slick-dots li.slick-active {
        margin-bottom: 50px;
    }
    .slick-dots li.slick-active button:before {
        color: #d08548 !important;
    }
    .slick-dots li.slick-active button:after {
        content: '';
        position: absolute;
        top: 30px; left: 10px;
        background-color: #d08548;
        height: 35px; width: 1px;
    }
    .slick-dots li button:before {
        font-family: 'Proxima Nova Rg'; 
        font-size: 14px;
        color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        content: '01';
        text-align: center;
        opacity: 1;
    }
    .slick-dots li:nth-child(2) button:before {
        font-family: 'Proxima Nova Rg'; 
        font-size: 14px;
        color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        content: '02';
        text-align: center;
        opacity: 1;
    }
    .slick-dots li:nth-child(3) button:before {
        font-family: 'Proxima Nova Rg'; 
        font-size: 14px;
        color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        content: '03';
        text-align: center;
        opacity: 1;
    }
    .slick-dots li:nth-child(4) button:before {
        font-family: 'Proxima Nova Rg'; 
        font-size: 14px;
        color: #fff;
        position: absolute;
        top: 0;
        left: 0;
        content: '04';
        text-align: center;
        opacity: 1;
    }
}