.action {
    margin-top: 120px;
    &_wrap {
        .row-flex();
    }
    &_left {
        .col();
        .size(3);
        .shift-left(1);
    }
    &_right {
        .col();
        .size(8);
        border-left: 2px solid #1d1b1e;
        position: relative;
        padding: 0 0 50px 30px;
        background: url(../img/paint.png) right -40px no-repeat;
        &::before {
            content: '';
            position: absolute;
            top: 0px; left: -2px;
            background-color: @crap;
            width: 2px; height: 35px;
        }
        &--h3 {
            font-size: 44px;
            .medium();
        }
        &--p {
            font-size: 15px;
            margin: 10px 0 25px;
        }
        &--btn {
            .btn();
            padding: 12px 30px;
        }
        .slick-prev {
            top: auto;
            bottom: -50px;
            right: 35px;
            left: auto;
        }
        .slick-next {
            top: auto;
            bottom: -50px;
            right: 5px;
            left: auto;
        }
    }
}