.btn_mnu {
    width: 50px;
    height: 18px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    cursor: pointer;
    position: relative;
    z-index: 101;
}
.btn_row {
    height: 2px;
    width: 60%;
    margin: 2px 0;
    background-color: #fff;
    position: relative;
    transition: all .5s;
}
.btn_mnu .btn_row:nth-child(2) {
    width: 30%;
}
.btn_mnu.active .btn_row:first-child {
    transform: rotate(-135deg);
    top: 6px;
}
.btn_mnu.active .btn_row:last-child {
    transform: rotate(-225deg);
    top: -6px;
}
.btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
}
#menu-burger {
    background-color: #181818;
    position: absolute;
    top: 0; left: -1000px;
    width: 50%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 1s all;
    z-index: 100;
}
#menu-burger.active {
    left: 0;
}
.menu-burger {
    &--li {
        margin: 20px 0;
        .xs-block({
            margin: 10px 0;
        });
    }
    &--a {
        font-size: 14px;
        color: #fff;
        text-transform: uppercase;
        display: inline-block;
        .xs-block({
            font-size: 15px;
        });
        &::after {
            display:block;
            content: '';
            border-bottom: 1px solid #fff;   
            transform: scaleX(0);    
            transform-origin: 100% 50%;
            transition: transform 450ms ease-in-out;
        }
        &:hover::after {
            transform: scaleX(1);
            transform-origin: 0% 50%;
        }
    }
    &--tel {
        position: absolute;
        top: 50px;
        a {
            margin: 5px;
            font-size: 24px;
            .medium();
        }
        div {
            text-align: right;
            margin-top: 5px;
        }
        p {
            color: #504c53;
            text-align: right;
            font-size: 14px;
            margin-bottom: 5px;
            .medium();
        }
    }
}

.header {
    &_wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 88px;
    }
    &_left {
        display: flex; 
        &--logo {
            margin-left: 35px;
        }
        &--span {
            margin-left: 80px;
            .xl({
                display: none;
            });
        }
    }
    &_right {
        &--ul {
            display: flex;
        }
        &--li {
            margin: 0 12px;
            .xl({
                margin: 0 10px;
            });
        }
        &--a {
            padding: 18px;
            transition: .3s all;
            font-size: 14px;
            text-transform: uppercase;
            .xl({
                padding: 15px;
            });
            &.active {
                border: 1px solid @crap;
            }
            &:hover {
                border: 1px solid @crap;
            }
        }
    }
}