.econom {
    background: url(../img/women.jpg) no-repeat;
    min-height: 744px;
    background-size: cover;
    background-position: center;
    &_wrap {
        min-height: 744px;
        border-bottom: 2px solid #1d1b1e;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            bottom: -2px; left: 75px;
            background-color: @crap;
            width: 35px; height: 2px;
        }
    }
    &_content {
        .shift-left(2);
        padding-top: 150px;
    }
    &_flex {
        display: flex;
        align-items: flex-start;
        span {
            .medium();
            font-size: 52px;
        }
        img {
            margin-left: 10px;
        }
    }
    &_span {
        font-size: 30px;
    }
    &_h3 {
        .medium();
        font-size: 52px;
        margin-top: 10px;
    }
    &_p {
        margin:35px 0 0 90px;
        max-width: 410px;
        font-size: 17px;
    }
    &_btn {
        .btn();
        margin:40px 0 0 90px;
    }
}