.service {
    margin-top: 140px;
    &_h2 {
        .h2();
    }
    &_slider {
        margin-top: 50px;
    }
    &_content {
        border-left: 2px solid #1d1b1e;
        position: relative;
        padding: 45px 0 30px 25px;
        &::before {
            content: '';
            position: absolute;
            top: 38px; left: -2px;
            background-color: @crap;
            width: 2px; height: 35px;
        }
    }
    &_h3 {
        .medium();
        font-size: 18px;
        text-transform: uppercase;
        margin-bottom: 25px;
    }
    &_h4 {
        color: @gray;
        font-size: 18px;
        line-height: 30px;
    }
    .slick-next{
        right:-25px;
        top: 30%;
    }
    .slick-prev{
        left:-30px;
        top: 30%;
    }
}
