.reset();
.container {
    .container();
}
.container-full {
    .container-full();
}
html {
    font-size: 16px;
    line-height: 1.2;
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: 'Proxima Nova Rg', 'Arial', sans-serif;
    background-color: #0e0c0f;
    color: #fff;
}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
button {
    background: none;
    border: none;
}
* {
	box-sizing: border-box;
}

@crap: #7e5337;
@gray: #655e68;

.xl(@content){
    @media screen and (max-width: 1440px){
        @content();
    }
}
.top {
    display: flex;
    align-items: center;
}
.h2() {
    .medium();
    font-size: 46px;
    text-transform: uppercase;
    position: relative;
    padding-left: 35px;
    &+span {
        margin-left: 25px;
        background-color: #1d1b1e;
        width: 60%; height: 1px;
    }
    &::after {
        content: '';
        position: absolute;
        top: -50px; left: 0px;
        background-color:#1d1b1e;
        width: 2px; height: 95px;
        z-index: -1;
    }
    &::before {
        content: '';
        position: absolute;
        top: 10px; left: 0px;
        background-color: @crap;
        width: 2px; height: 35px;
    }
}
.btn() {
    display: inline-block;
    padding: 12px 20px;
    border: 1px solid @crap;
    text-transform: uppercase;
    font-size: 17px;
    transition: .3s all;
    &:hover {
        background-color: @crap;
    }
}

.slick-next{
    z-index:100;background:url(../img/arrow-right.png) no-repeat;width:11px;height:17px;
}
.slick-next::before{
    display:none
}
.slick-next:hover{
    background:url(../img/arrow-right.png) no-repeat;
    width:11px;height:17px;
}
.slick-prev{
    z-index:100;background:url(../img/arrow-left.png) no-repeat;width:11px;height:17px;
}
.slick-prev::before{
    display:none
}
.slick-prev:hover{
    background:url(../img/arrow-left.png) no-repeat;
    width:11px;height:17px;
}